 .container1
{
    padding: 40px 50px;
    width: 100%;
    background: #f1f1f1;
    border-bottom: 1px solid #9a9a9a;
} 
.container1 .emptycart
{
    margin-top: 30px;
    margin-left: 40%;
}
.container1 .emptycart h2
{
    font-size: 32px;
    color: #010f1c;
    text-transform: uppercase;
    font-weight: 300;
    margin-bottom: 30px;
}
.container1 .emptycart .emptycartbtn
{
    margin-left: 70px;
    text-decoration: none;
    color: #fff;
    padding: 10px 20px;
    background: #010f1c;
} 
.content1{
    display: flex;
} 

.wish_item1
{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.wish_item1
{
    padding: 10px 20px;
    overflow: hidden;
    width: 300px;
    height: 400px;
    border: 1px solid #e2e0e0;
    margin-bottom: 20px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 20px;
}
.wish_item1:hover
{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.img_box
{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e2e0e0;
}
 .img_box img
{
    width: 280px;
    height: 230px;
    margin-right: 50px;
    transition: 0.5s all linear;
}
 .img_box img:hover
{
    transform: scale(1.2);
}
.detail
{
    margin-top: 10px;
}
 .detail h3
{
    font-size: 16px;
    margin-top: 5px;
    color: #010f1c;
    transition: 0.5s;
}
.detail h3
{
    color: #0989ff;
}
.detail h4
{
    margin-top: 5px;
    font-size: 14px;
    color: #0989ff;
    letter-spacing: 1px;
}